import "./App.css";
import Glassform from "./compo/Glassform";
import Register from "./compo/Register";
import About from "./compo/About";
import Navbar from "./compo/Navbar";
import Home from "./compo/Home";
import Datac from "./compo/bdata/Data";
import { Routes, Route } from "react-router-dom";
import Contact from "./compo/Contact";

function mycard(val) {
  return (
    <Home
      key={val.id}
      img_src={val.img_src}
      cate={val.cate}
      title={val.title}
      link={val.link}
    />
  );
}
const books = Datac.map(mycard);

function App() {
  return(
    <>
    
      <Routes>
        <Route
         exact
         path="/book"
         element={
          <>
          <Navbar val="My Order"  link='/login'/> 
         {books}
          </>
          }
         
        />
        <Route
         exact
         path="/about"
         element={
          <>
          <Navbar val="My Order"  link="/login"/> 
          <About/>
          </>
         
        }
         
        />
        <Route
         exact
         path="/contact"
         element={
          <>
          <Navbar val="My Order"  link='/'/> 
          <Contact/>
          </>
        
        }
         
        />

        <Route exact path="/" element={
          <>
        <Navbar val="Log In"  link='/login'/> 
        <Register/>
        </>
        } />

        <Route exact path="/login" element={
          <>
        <Navbar val="Sign up" link='/'/> 
        <Glassform/>
        </>
        } />
      </Routes>
    </>
  );
}

export default App;
